import style, { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
 html {
  background: #3d3d3d;
}

body {
  background: #3d3d3d;
  font: 12px 'Lucida Grande', Arial, sans-serif;
}
`

export const Logo = style.div`
    background: url('https://border-radius.com/border_radius.png') top left no-repeat transparent;
    width: 283px;
    height: 35px;
    text-indent: -9999px;
    margin: 65px auto 15px auto;
`;

export const Container = style.div`
    width: 550px;
    margin: 50px auto;
    position: relative;
`;

export const Surface = style.div`
background: #e9eaee;
border: 2px solid #ffffff;
padding: 44px 40px 20px 40px;
width: 460px;
margin: 0 auto;
text-align: center;
box-shadow: 1px 2px 6px rgba(0,0,0, 0.5);
-moz-box-shadow: 1px 2px 6px rgba(0,0,0, 0.5);
-webkit-box-shadow: 1px 2px 6px rgba(0,0,0, 0.5);
width: 430px;
height: 240px;
`;

export const Input = style.input`   
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
`;

export const TopLeft = style.div`
    margin-left: -31px;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 20px;
    padding-top: 10%;
    width: 100%;
`;

export const Phrase = style.p`
    color: #FFF;
    font-size: 120%;
`;